
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#app div {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.main-page {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
}
.main-page__open-modal {
  margin-bottom: 50px;
}

.contact-add-modal__content .ui-field {
  padding: 10px 0;
}
.contact-add-modal__content .ui-field__title {
  width: 40%;
}
.contact-add-modal__content .ui-field__input {
  width: 60%;
}

.table {
  width: 500px;
}

.table-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.table-row > div {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
.table-row > .table-row {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-left: 20px;
}
.table-cell__name,
.table-cell__phone {
  border: 1px solid black;
  padding: 7px 5px 7px 20px;
  text-align: left;
  cursor: pointer;
  white-space: nowrap;
}
.table-cell__name {
  width:calc(100% - 267px);
}
.table-cell__phone {
  width: 267px;
}
.table-cell__has-director {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.ui-button {
  border-radius: 13px;
  padding: 5px 16px;
  border: 1px solid black;
  background: lightgray;
  cursor: pointer;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
}

.ui-field {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-transform: capitalize;
}

.ui-input {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.ui-input input {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}

.ui-modal__outer {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 100;
  background: rgba(0,0,0,0.2);
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ui-modal {
  width: 450px;
  background: #fff;
  border: 1px solid black;
  padding: 20px;
  text-align: left;
  border-radius: 2px;
}
.ui-modal__heading {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.ui-modal__title {
  font-size: 20px;
}
.ui-modal__close {
  cursor: pointer;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  font-size: 20px;
  line-height: 16px;
}
.ui-modal__body {
  padding: 30px 0;
}

.ui-select {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.ui-select select {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
